@font-face {
	font-family: 'High Tide';
	src: url('./media/High-Tide_Sans.otf') format('opentype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'High Tide';
	src: url('./media/High-Tide_Bold.otf') format('opentype');
	font-weight: bold;
	font-style: normal;
}

body {
	font-family: 'High Tide', sans-serif;

	background: #fff;
	color: hsl(224, 52%, 26%);
}

a {
	text-decoration: none;
	color: currentColor;
}

::-webkit-input-placeholder {
  color: rgba(0, 0, 0, .5);
}

.hidden { display: none; }

.alignleft { float: left !important; }
.alignright { float: right !important; }

.left   { text-align: left; }
.center { text-align: center; }
.right  { text-align: right; }
.middle {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.wrapper {
	width: 100%;
	max-width: 67.5rem;
	padding-left: 1.5rem;
	padding-right: 1.5rem;
	margin: auto;
}

.row::after {
	content: '';
	display: block;
	clear: both;
}

.spacing {
	display: block;
	height: 4em;
}

.fullheight {
	height: 100vh;
}

[class*="col-"] {
	display: block;
	float: left;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	min-height: 1px;
	padding: 0 1rem;

	&.nopadding {
		padding: 0;
	}
}

.col-1  { width:   8.33333%; }
.col-2  { width:  16.66666%; }
.col-3  { width:  25%;       }
.col-4  { width:  33.33333%; }
.col-5  { width:  41.66666%; }
.col-6  { width:  50%;       }
.col-7  { width:  58.33333%; }
.col-8  { width:  66.66666%; }
.col-9  { width:  75%;       }
.col-10 { width:  83.33333%; }
.col-11 { width:  91.66666%; }
.col-12 { width: 100%;       }

