	.left-l   { text-align: left; }
	.center-l { text-align: center; }
	.right-l  { text-align: right; }
	.middle-l {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
	}

	.hidden-l { display: none; }
}

@media screen and (min-width: 481px) and (max-width: 720px) {
	.col-m-1  { width:   8.33333%; }
